<template>
    <div class="ai-assistant-phone-categories">
        <div
            class="ai-assistant-phone-categories-item"
            :class="
                activeKey === tab.key
                    ? 'ai-assistant-phone-categories-item-selected'
                    : ''
            "
            v-for="tab in tabs"
            :key="tab.key"
            @click="() => clickHandle(tab.key)"
        >
            {{ tab.tab }}
        </div>
    </div>
</template>
<script lang="ts" setup>
import { onMounted, ref, watch, defineProps, PropType } from 'vue';
import { getDomainList } from '@/api/market';

const DEFAULT_TABS: TabsType = [
    { key: 'all', tab: '全部' },
    { key: 'my', tab: '我的' },
];
const props = defineProps({
    changeHandle: Function as PropType<(activeKey: string) => void>,
    defaultTabs: Array as PropType<TabsType>,
});

export type TabsType = {
    key: string;
    tab: string;
}[];

const activeKey = ref<string>('all');
const tabs = ref<TabsType>(props.defaultTabs || DEFAULT_TABS);

onMounted(() => {
    // 获取分类
    getDomainList().then((response) => {
        const domains = (response.data || []).map((item) => ({
            key: item.code,
            tab: item.name,
        }));
        tabs.value = [...(props.defaultTabs || DEFAULT_TABS), ...domains];
    });
});

const clickHandle = (target: string) => {
    activeKey.value = target;
    if (props.changeHandle) props.changeHandle(target);
};
</script>
