<!-- eslint-disable vue/html-indent -->
<template>
    <div
        class="assistant-market-phone-assistants-card"
        :class="{
            'assistant-market-phone-assistants-card-disabled': !data.userUsable,
            'assistant-market-phone-assistants-card-mobile': isMobile(),
        }"
        @click="useHandle"
    >
        <div class="assistant-market-phone-assistants-card-header">
            <div class="assistant-market-phone-assistants-card-avatar">
                <AssistantAvarat
                    :icon="data.agentIcon"
                    :iconBackground="data.iconBackground"
                />
            </div>

            <div class="assistant-market-phone-assistants-card-header-content">
                <div
                    class="assistant-market-phone-assistants-card-header-title"
                >
                    <a-tooltip :title="data.agentName">
                        {{ data.agentName }}
                    </a-tooltip>
                </div>
                <a-tooltip :title="data.agentDesc">
                    <div
                        class="assistant-market-phone-assistants-card-header-note"
                    >
                        {{ data.agentDesc }}
                    </div>
                </a-tooltip>
            </div>
        </div>
        <div
            class="assistant-market-phone-assistants-card-tips"
            v-if="getAgentTypeValue(data.agentType) !== 'personal'"
        >
            <div class="assistant-domain">@{{ data.domainName }}</div>
            <div class="assistant-hot-number">
                <hot style="margin-right: 3px" />
                {{ getNumber(heat) }}
            </div>
        </div>
        <div class="assistant-market-phone-assistants-card-tips" v-else>
            <div class="assistant-domain">@个人助手</div>
        </div>
        <div
            class="assistant-market-phone-assistants-card-actions"
            v-if="
                showAction && getAgentTypeValue(data.agentType) !== 'personal'
            "
        >
            <div
                class="assistant-market-phone-assistants-card-action-item"
                @click="(e) => thumbsUpHandle(e)"
            >
                <img
                    width="16"
                    height="16"
                    :src="!userThumbsUp ? thumbsUpIcon : thumbsUpIconFill"
                />
                <span
                    class="assistant-market-phone-assistants-card-action-item-number"
                >
                    {{ getNumber(thumbsUp) }}
                </span>
            </div>
            <div
                v-if="data.agentCode !== 'super'"
                class="assistant-market-phone-assistants-card-action-item"
                style="margin-left: 13px"
                @click="(e) => favoriteHandle(e)"
            >
                <img
                    width="16"
                    height="16"
                    :src="!userFavorites ? favoriteIcon : favoriteIconFill"
                />

                <span
                    class="assistant-market-phone-assistants-card-action-item-number"
                >
                    {{ getNumber(favorites) }}
                </span>
            </div>
        </div>
    </div>
</template>
<script setup lang="ts">
import { message } from '@hwork/ant-design-vue';
import thumbsUpIcon from '@/assets/icons/thumbs-up.svg';
import thumbsUpIconFill from '@/assets/icons/thumbs-up-fill.svg';
import favoriteIcon from '@/assets/icons/favorite.svg';
import favoriteIconFill from '@/assets/icons/favorite-fill.svg';
import hot from './icons/hot.vue';
import { useRoute, useRouter } from 'vue-router';
import { PropType, computed, ref, inject } from 'vue';
import { doFavorite, doThumbsUp, doUse, AgentType } from '@/api/market';
import AssistantAvarat from '@/components/AssistantAvarat/index.vue';
import { isMobile } from '@/utils/util';

const props = defineProps({
    assistant: Object as PropType<Record<string, any>>,
    agentType: String as PropType<string>,
    showAction: {
        type: Boolean as PropType<boolean>,
        default: true,
    },
    clickHandle: {
        type: Function,
        default: () => {},
    },
    conversationRunning: Boolean,
});

const router = useRouter();
const route = useRoute();
const changeActiveKey = inject('changeActiveKey');
const thumbsUp = ref<number>(props.assistant?.thumbsUp || 0);
const favorites = ref<number>(props.assistant?.favorites || 0);
const heat = ref<number>(props.assistant?.heat || 0);
const userFavorites = ref<boolean>(props.assistant?.userFavorites || false);
const userThumbsUp = ref<boolean>(props.assistant?.userThumbsUp || false);

const getAgentTypeValue = (target: string) => {
    if (props.agentType === 'personal') {
        return 'personal';
    }
    return target;
};

const thumbsUpHandle = (e: Event) => {
    e.stopPropagation();
    if (!data.value.userUsable) {
        return;
    }

    userThumbsUp.value = !userThumbsUp.value;
    thumbsUp.value += userThumbsUp.value ? 1 : -1;
    doThumbsUp(props.assistant?.id, userThumbsUp.value);
};

const favoriteHandle = (e: Event) => {
    e.stopPropagation();
    if (!data.value.userUsable) {
        return;
    }

    userFavorites.value = !userFavorites.value;
    favorites.value += userFavorites.value ? 1 : -1;
    doFavorite(props.assistant?.id, userFavorites.value);
};

const useHandle = async () => {
    if (props.conversationRunning) {
        message.info('正在生成中，请先停止回答');
        return;
    }
    if (!data.value.userUsable) {
        message.error(
            '当前助手无权限，无法正常使用。可联系所属领域管理员添加权限后再使用。',
        );
        return;
    }

    if (
        route?.query?.agent === 'super' &&
        props.assistant?.agentCode === 'super' &&
        typeof changeActiveKey === 'function'
    ) {
        changeActiveKey('chat');
        return;
    }
    doUse(props.assistant?.id);
    if (typeof props.clickHandle === 'function') {
        props.clickHandle();
    }

    router.push({
        path: '/chat/new',
        query: {
            agent: props.assistant?.agentCode,
        },
    });

    // 跳转到聊天
    // router.push({
    //     path: `/chat/${chatGroup.groupCode}`,
    //     query: {
    //         agent: props.assistant?.groupType,
    //     },
    // });
};

const getNumber = (num: number) => {
    return num > 999 ? '999+' : num;
};

const data = computed<Record<string, any>>(() => {
    return props.assistant!;
});
</script>
<style lang="less" scoped>
@import url('./index.less');
</style>
