<template>
    <div
        class="market-search"
        :class="props.modelValue && 'market-search-has-value'"
    >
        <input
            class="market-search-input"
            :placeholder="placeholder"
            :value="props.modelValue"
            @input="updateValue"
        />
        <div class="market-search-after-search">
            <h-icon-search />
        </div>
        <div class="market-search-after-clean" @click="cleanHandle">
            <h-icon-close-circle-fill />
        </div>
    </div>
</template>
<script setup lang="ts">
import { PropType, computed } from 'vue';
import '@hwork/icon/search';
import '@hwork/icon/close-circle-fill';

import { ref } from 'vue';

const timer = ref<null | number>(null);
const props = defineProps({
    modelValue: String as PropType<string>,
    placeholder: String as PropType<string>,
});

const emit = defineEmits<{
    (e: 'update:modelValue', value: string): void;
}>();

const placeholder = computed(() => {
    return props.placeholder || '请输入要搜索的智能体';
});

const updateValue = (event: Event) => {
    if (timer.value !== null) {
        clearTimeout(timer.value);
    }
    timer.value = setTimeout(() => {
        const target = event.target as HTMLInputElement | null;
        if (target) {
            emit('update:modelValue', target.value);
        }
    }, 300);
};

const cleanHandle = () => {
    emit('update:modelValue', '');
};
</script>
<style lang="less" scoped>
.market-search {
    margin-top: 16px;
    margin-bottom: 12px;
    width: 320px;
    height: 30px;
    border-radius: 6px;
    background-color: #fff;
    border: 1px solid #fff;
    overflow: hidden;
    display: flex;
    align-items: center;
    &:focus-within {
        border: 1px solid rgb(22, 93, 255) !important;
    }
    &:hover {
        border: 1px solid #4081ff;
    }

    &-input {
        border: 0px solid;
        height: 30px;
        outline: none;
        padding: 0 12px;
        font-size: 14px;
        flex-grow: 1;
        width: 0;
        caret-color: rgb(22, 93, 255);
        &::placeholder {
            color: rgb(134, 144, 156);
        }
    }
    &-after-search {
        width: 32px;
        flex-shrink: 1;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: rgb(134, 144, 156);
    }
    &-after-clean {
        display: none;
        width: 32px;
        flex-shrink: 1;
        height: 30px;
        justify-content: center;
        align-items: center;
        color: rgb(201, 205, 212);
    }
}
.market-search-has-value {
    &:hover {
        .market-search-after-clean {
            display: flex;
        }
        .market-search-after-search {
            display: none;
        }
    }
    &:focus-within {
        .market-search-after-clean {
            display: flex;
        }
        .market-search-after-search {
            display: none;
        }
    }
}
</style>
