<template>
    <div class="assistant-market-phone">
        <div class="assistant-market-phone-categories">
            <Categories
                :change-handle="changeDomainList"
                :defaultTabs="DEFAULT_TABS"
            />
        </div>
        <a-spin :spinning="loading">
            <div
                class="assistant-market-phone-assistants"
                v-if="assistants.length"
            >
                <a-row>
                    <a-col
                        :span="24"
                        v-for="assistant in assistants"
                        :key="assistant.id"
                    >
                        <AssistantSelfCard
                            v-if="showSelf"
                            :assistant="assistant"
                            @delete="refreshList"
                            :conversation-running="conversationRunning"
                        />
                        <AssistantCard
                            v-else
                            :agentType="
                                domains[0] === '个人助手' ? 'personal' : ''
                            "
                            :assistant="assistant"
                            :conversation-running="conversationRunning"
                        />
                    </a-col>
                </a-row>
                <div class="assistant-market-create-assistant">
                    <img
                        :src="createAssistantIcon"
                        alt="创建助手"
                        @click="createAssistant"
                    />
                </div>
            </div>
            <div class="assistant-market-empty-data-phone" v-else>
                <img :src="emptyDataImage" />
                <span>暂无数据</span>
            </div>
        </a-spin>
    </div>
</template>
<script lang="ts" setup>
import { ref, watch, computed, onMounted, defineProps, PropType } from 'vue';
import { useRoute } from 'vue-router';
import Categories from '../Categories/mobile.vue';
import AssistantCard from './AssistantCard/index.vue';
import AssistantSelfCard from '@/components/AssistantSelfCard/index.vue';
import emptyDataImage from '@/assets/images/empty-data.png';
import { AgentType, getMarketListDrawer } from '@/api/market';
import createAssistantIcon from '@/assets/icons/create-assistant.svg';
import { useRouter } from 'vue-router';

const props = defineProps({
    search: {
        type: String as PropType<string>,
        default: '',
    },
    conversationRunning: Boolean,
});

const router = useRouter();
export type Tabs = {
    key: string;
    tab: string;
}[];

const DEFAULT_TABS: Tabs = [
    { key: 'all', tab: '全部' },
    { key: '最近使用', tab: '最近使用' },
    { key: '我的收藏', tab: '我的收藏' },
    { key: '个人助手', tab: '个人助手' },
];

const route = useRoute();
const searchText = ref<string>('');
const type = ref<string>('我的使用');
const domains = ref<string[]>([]);
const assistants = ref<Record<string, any>[]>([]);
const sort = ref<string>('HEAT_DESC');
const loading = ref<boolean>(true);
const isMounted = ref<boolean>(false);

const changeDomainList = (domain: string) => {
    if (domain === 'all') {
        domains.value = [];
    } else {
        domains.value = [domain];
    }
};

const refreshList = () => {
    type.value = '个人助手';
    domains.value = ['my'];
};

const showSelf = computed(() => {
    return type.value === '个人助手' && domains.value[0] === 'my';
});

const createAssistant = () => {
    router.push({
        path: `/create/assistant`,
    });
};

watch(
    () => [
        domains.value,
        props.search,
        searchText.value,
        type.value,
        sort.value,
    ],
    () => {
        if (!isMounted.value && route.query?.type) return;
        loading.value = true;
        getMarketListDrawer({
            pageNumber: 1,
            pageSize: 50,
            domainList: domains.value,
            agentName: props.search || searchText.value,
            type: type.value,
            orderBy: sort.value,
        })
            .then((response) => {
                assistants.value = (response.data?.items ||
                    response?.data ||
                    []) as AgentType[];
            })
            .finally(() => {
                loading.value = false;
            });
    },
    {
        immediate: true,
        deep: true,
    },
);
onMounted(() => {
    const type = route.query?.type;
    isMounted.value = true;
    if (type) {
        refreshList();
    }
});
</script>
<style lang="less" scoped>
@import url('./index.less');
</style>
