<template>
    <component :is="modalComponent" ref="modalRef">
    </component>
</template>
<script setup lang="ts">
import { computed, ref } from 'vue';
import { showMobile } from '@/utils/util';
import Modal from './Modal.vue';
import ModalMobile from './ModalMobile.vue';

const isMobile = showMobile();
const modalRef = ref<
    InstanceType<typeof Modal> | InstanceType<typeof ModalMobile>
>();
const modalComponent = computed(() => {
    return isMobile ? ModalMobile : Modal;
});
const show = () => {
    modalRef.value?.show();
};
const hide = () => {
    modalRef.value?.hide();
};
defineExpose({ show, hide });
</script>
