<template>
    <a-tabs v-model:activeKey="activeKey">
        <a-tab-pane v-for="tab in tabs" :key="tab.key" :tab="tab.tab" />
    </a-tabs>
</template>
<script lang="ts" setup>
import { onMounted, ref, watch, defineProps, PropType } from 'vue';
import { getDomainList } from '@/api/market';

const DEFAULT_TABS: Tabs = [
    { key: 'all', tab: '全部' },
    { key: 'my', tab: '我的' },
];
const props = defineProps({
    changeHandle: Function as PropType<(activeKey: string) => void>,
    defaultTabs: Array as PropType<Tabs>,
});

export type Tabs = {
    key: string;
    tab: string;
}[];

const activeKey = ref<string>('all');
const tabs = ref<Tabs>(props.defaultTabs || DEFAULT_TABS);

onMounted(() => {
    // 获取分类
    getDomainList().then((response) => {
        const domains = (response.data || []).map((item) => ({
            key: item.code,
            tab: item.name,
        }));
        tabs.value = [...(props.defaultTabs || DEFAULT_TABS), ...domains];
    });
});

watch(
    () => activeKey.value,
    () => {
        if (props.changeHandle) props.changeHandle(activeKey.value);
    },
);
</script>
<style lang="less" scoped>
:deep(.@{ant-prefix}-tabs-tab) {
    color: rgb(78, 89, 105);
    &:hover {
        color: rgb(64, 128, 255);
    }
}
:deep(
        .@{ant-prefix}-tabs-tab.@{ant-prefix}-tabs-tab-active
            .@{ant-prefix}-tabs-tab-btn
    ) {
    text-shadow: none;
    font-weight: 600;
}
</style>
