<!-- eslint-disable vue/html-indent -->
<template>
    <div class="assistant-market-phone-card-wrapper">
        <div
            class="assistant-market-phone-card"
            :class="!data.userUsable && 'assistant-market-phone-card-disabled'"
            :style="{ left: `${left}px` }"
            @touchstart="touchstartEvent"
            @touchend="touchendEvent"
            @click="useHandle"
        >
            <div class="assistant-market-phone-card-header">
                <div
                    class="assistant-market-phone-card-avatar"
                    :style="{ backgroundColor: data.iconBackground }"
                >
                    <img :src="data.agentIcon" />
                </div>
                <div class="assistant-market-phone-card-header-content">
                    <div class="assistant-market-phone-card-header-title">
                        {{ data.agentName }}
                    </div>
                    <a-tooltip :title="data.agentDesc">
                        <div class="assistant-market-phone-card-header-note">
                            {{ data.agentDesc }}
                        </div>
                    </a-tooltip>
                </div>
            </div>
        </div>
        <div
            v-show="showBtn"
            class="assistant-market-phone-card-operate-container"
            :style="{ left: showBtn ? '-60px' : '0' }"
        >
            <a class="operate-btn" @click="toEdit">编辑</a>
        </div>
        <div
            v-show="showBtn"
            class="assistant-market-phone-card-operate-container danger"
        >
            <a class="operate-btn" @click="modalRef?.show()">删除</a>
        </div>
        <Modal
            ref="modalRef"
            content="助手删除后无法使用该助手，且删除后不可恢复，是否确认删除？"
            @confirm="handlerDelete"
        />
    </div>
</template>
<script setup lang="ts">
import '@hwork/icon/star';
import '@hwork/icon/thumb-up';
import { PropType, computed, ref } from 'vue';
import { message } from '@hwork/ant-design-vue';
import { useRouter } from 'vue-router';
import Modal from '@/components/Modal/index.vue';
import { doUse, deleteAssistant } from '@/api/market';

const router = useRouter();
const props = defineProps({
    assistant: Object as PropType<Record<string, any>>,
    conversationRunning: Boolean,
});
const emit = defineEmits<{
    (e: 'edit', data: Record<string, any>): void;
    (e: 'delete'): void;
}>();

const left = ref(0);
const startX = ref(0);
const showBtn = ref(false);
const modalRef = ref<InstanceType<typeof Modal>>();
const touchstartEvent = (e: any) => {
    startX.value = e.changedTouches[0].clientX;
};
const touchendEvent = (e: any) => {
    const endX = e.changedTouches[0].clientX;
    if (startX.value - endX >= 30) {
        showBtn.value = true;
        left.value = -120;
    }
    if (startX.value - endX <= -30) {
        showBtn.value = false;
        left.value = 0;
    }
};
const useHandle = async () => {
    if (props.conversationRunning) {
        message.info('正在生成中，请先停止回答');
        return;
    }
    if (!data.value.userUsable) {
        message.error(
            '当前助手无权限，无法正常使用。可联系所属领域管理员添加权限后再使用。',
        );
        return;
    }
    doUse(props.assistant?.id);

    router.push({
        path: '/chat/new',
        query: {
            agent: props.assistant?.agentCode,
        },
    });

    // 跳转到聊天
    // router.push({
    //     path: `/chat/${chatGroup.groupCode}`,
    //     query: {
    //         agent: props.assistant?.groupType,
    //     },
    // });
};
const handlerDelete = async () => {
    await deleteAssistant(props.assistant?.agentCode);
    emit('delete');
    modalRef.value?.hide();
};
async function toEdit() {
    router.push({
        path: `/create/assistant`,
        query: {
            agentCode: props.assistant?.agentCode,
        },
    });
}
const data = computed<Record<string, any>>(() => {
    return props.assistant!;
});
</script>
<style lang="less" scoped>
@import url('./Mobile.less');
</style>
