<template>
    <a-modal
        v-model:visible="modalVisible"
        :closable="false"
        :footer="null"
        :width="406"
        wrapClassName="assistant-modal"
    >
        <BlockTitle :title="props.title" class="modal-title" />
        <p class="modal-content">{{ props.content }}</p>
        <a-space class="footer-wrap">
            <a-button class="btn" @click="modalVisible = false">
                取消
            </a-button>
            <a-button class="btn" type="primary" @click="$emit('confirm')">
                确认
            </a-button>
        </a-space>
    </a-modal>
</template>
<script setup lang="ts">
import { ref } from 'vue';
import { BlockTitle } from '@deep/vue3';
const props = defineProps<{ title: string; content: string }>();
const modalVisible = ref(false);
const show = () => {
    modalVisible.value = true;
};
const hide = () => {
    modalVisible.value = false;
};
defineExpose({ show, hide });
</script>
<style lang="less">
.assistant-modal {
    .modal-title {
        min-height: 24px;
    }
    .modal-content {
        margin-top: 8px;
        color: #4e5969;
        font-size: 14px;
    }
    .footer-wrap {
        display: flex;
        justify-content: flex-end;
        .btn {
            width: 80px;
        }
    }
}
</style>
